import React, { Component } from 'react';
import { UncontrolledAlert, Navbar, NavbarBrand, Nav, NavItem, NavLink, Jumbotron, Container, Button, Progress, Card, CardImg, CardImgOverlay, CardTitle, CardText, Row, Col, Badge } from 'reactstrap';
import ScrollableAnchor, { configureAnchors } from 'react-scrollable-anchor';
import { Icon } from 'react-fa';
import background from './background.jpg';
import background02 from './background.png';
import backgroundProjectSBHSMaps from './projects/01.png';
import backgroundProjectBench from './projects/02.png';
const NAVBAR_BG_COLOR = "rgba(0, 0, 0, 0.5)"
class App extends Component
{
    constructor()
    {
        super();
        configureAnchors({ offset: -75 });
    }
    render()
    {
        let skills_bar_style = { width: 200, backgroundColor: "rgba(255, 255, 255, 0.1)", marginBottom: 5 };
        return (
            <div className="App">
                <div id="background-image" style={{
                    position: "fixed",
                    top: 0,
                    left: 0,
                    zIndex: -1,
                    backgroundImage: "url(" + background + ")",
                    height: "100%",
                    width: "100%",
                    backgroundAttachment: "fixed",
                    backgroundSize: "cover",
                    backgroundRepeat: "no-repeat",
                    backgroundPosition: "50% 50%",
                }}></div>
                <div id="background-vignette" style={{
                    position: "fixed",
                    top: 0,
                    left: 0,
                    zIndex: -1,
                    height: "100%",
                    width: "100%",
                    backgroundAttachment: "fixed",
                    background: "radial-gradient(rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.75), rgba(0, 0, 0, 1))"
                }}></div>
                <div id="body-top" style={{
                    height: window.innerHeight - 74,
                    backgroundColor: NAVBAR_BG_COLOR,
                    color: "white",
                    textAlign: "center"
                }}>
                    <div style={{ height: (window.innerHeight / 2) - 150 }}>
                        <ScrollableAnchor id="home">
                            <h1></h1>
                        </ScrollableAnchor>
                    </div>
                    <h1 className="display-3" style={{ textAlign: "right", width: "52%" }}>{"Shivan"}</h1>
                    <h1 className="display-3" style={{ textAlign: "left", marginLeft: "48%", width: "52%", verticalAlign: "middle" }}><span>{"Modha"}</span></h1>
                    <h5 style={{ marginLeft: "25%", width: "50%", color: "rgba(255, 255, 255, 0.5)", marginTop: 25 }}>Full Stack Web Developer</h5>
                    <h5 style={{ marginLeft: "25%", width: "50%", color: "rgba(255, 255, 255, 0.5)" }}>Graphics Designer</h5>
                    <h5 style={{ marginLeft: "25%", width: "50%", color: "rgba(255, 255, 255, 0.5)" }}>Passionate Programmer</h5>
                </div>
                <UncontrolledAlert color="danger" style={{ position: "absolute", top: 0, width: "100%", height: 50 }} >Under Construction</UncontrolledAlert>
                <Navbar id="body-nav" style={{ backgroundColor: "black" }} inverse toggleable fixed="bottom" className="sticky-top">
                    <Button outline color="primary" onClick={(e) => {  }} style={{ border: "none", borderRadius: 100, width: 50, height: 50 }}><Icon name="bars" style={{ color: "white" }} /></Button>
                    <div style={{ width: 100 }}></div>
                    <Nav className="ml-auto" navbar>
                        <NavItem><NavLink href="#about">About</NavLink></NavItem>
                        <NavItem><NavLink href="#projects">Projects</NavLink></NavItem>
                        <div style={{ width: window.innerWidth * 0.05 }}></div>
                    </Nav>
                    <NavbarBrand href="#home" style={{ padding: 0, margin: 0 }}>Shivan <span>Modha</span></NavbarBrand>
                    <Nav className="mr-auto" navbar>
                        <div style={{ width: window.innerWidth * 0.05 }}></div>
                        <NavItem><NavLink href="#contact">Examples</NavLink></NavItem>
                        <NavItem><NavLink href="https://github.com/shivanmodha">Contact</NavLink></NavItem>
                    </Nav>
                    <div style={{ width: 100 }}></div>
                    <Button outline color="success" onClick={(e) => { window.location = "https://github.com/shivanmodha/" }} style={{ border: "none", borderRadius: 100, width: 50, height: 50 }}><Icon name="github" style={{ color: "white" }} /></Button>
                    {/*<Button outline color="primary" onClick={(e) => {  }} style={{ border: "none", borderRadius: 100, width: 50, height: 50 }}><Icon name="linkedin" style={{ color: "white" }} /></Button>*/}
                    {/*<Button outline color="primary" onClick={(e) => {  }} style={{ border: "none", borderRadius: 100, width: 50, height: 50 }}><Icon name="cog" style={{ color: "white" }} /></Button>*/}
                </Navbar>
                <div id="body-bottom">
                    <Jumbotron fluid style={{ padding: 0, backgroundColor: "rgba(0, 0, 0, 0.25)", background: "url(" + background02 + ")", backgroundAttachment: "fixed", backgroundSize: "cover", backgroundRepeat: "no-repeat", backgroundPosition: "50% 50%" }}>
                        <div style={{ minHeight: window.innerHeight, backgroundColor: "rgba(0, 0, 0, 0.75)", height: "100%", margin: 0 }}>
                            <Container style={{ paddingTop: 50, paddingBottom: 50 }}>
                                <ScrollableAnchor id="about">
                                    <h1 className="display-3" style={{ color: "rgba(255, 255, 255, 1)" }}>About <span>Me</span></h1>
                                </ScrollableAnchor>
                                <p className="lead">
                                    I am a programmer and graphics designer with a passion to build responsive applications from scratch. I love everything technical, and am obsessed with minimalistic elegance. As computers change, so must I! I will forever keep up with new technologies. How else am I to have fun?
                                </p>
                                <h2 style={{ textAlign: "center" }}>Technical <span>Expertise</span></h2>
                                <table style={{ width: "100%", textAlign: "center" }}>
                                    <tr>
                                        <td>Mobile</td>
                                        <td>Web</td>
                                        <td>Desktop/Server</td>
                                        <td>Graphics</td>
                                    </tr>
                                    <tr style={{ height: 200, textAlign: "center" }}>
                                        <td>
                                            <div style={{ transformOrigin: "top left", transform: "rotate(-90deg)", position: "relative", top: 149, left: "calc(50% - 30px)" }}>
                                                <Progress color="warning" value={90} style={skills_bar_style}>Android (Java)</Progress>
                                                <Progress color="warning" value={100} style={skills_bar_style}>XML</Progress>
                                                <Progress color="info" value={50} style={skills_bar_style}>Virtual Reality</Progress>
                                            </div>
                                        </td>
                                        <td>
                                            <div style={{ transformOrigin: "top left", transform: "rotate(-90deg)", position: "relative", top: 180, left: "calc(50% - 60px)" }}>
                                                <Progress color="warning" value={100} style={skills_bar_style}>HTML5</Progress>
                                                <Progress color="primary" value={100} style={skills_bar_style}>CSS3</Progress>
                                                <Progress color="success" value={100} style={skills_bar_style}>JavaScript (JS)</Progress>
                                                <Progress color="danger" value={90} style={skills_bar_style}>React (JSX)</Progress>
                                                <Progress color="success" value={75} style={skills_bar_style}>Firebase</Progress>
                                                <Progress color="success" value={50} style={skills_bar_style}>MongoDB</Progress>
                                            </div>
                                        </td>
                                        <td>
                                            <div style={{ transformOrigin: "top left", transform: "rotate(-90deg)", position: "relative", top: 170, left: "calc(50% - 50px)" }}>
                                                <Progress color="info" value={100} style={skills_bar_style}>C# (.NET)</Progress>
                                                <Progress color="success" value={70} style={skills_bar_style}>C++/CLI</Progress>
                                                <Progress color="warning" value={100} style={skills_bar_style}>Java</Progress>
                                                <Progress color="danger" value={100} style={skills_bar_style}>NodeJS (JS)</Progress>
                                                <Progress color="primary" value={90} style={skills_bar_style}>Python</Progress>
                                            </div>
                                        </td>
                                        <td>
                                            <div style={{ transformOrigin: "top left", transform: "rotate(-90deg)", position: "relative", top: 159, left: "calc(50% - 40px)" }}>
                                                <Progress color="success" value={75} style={skills_bar_style}>DirectX 12 (C++)</Progress>
                                                <Progress color="info" value={90} style={skills_bar_style}>Unity3D (C#)</Progress>
                                                <Progress color="danger" value={80} style={skills_bar_style}>WebGL (JS)</Progress>
                                                <Progress color="primary" value={60} style={skills_bar_style}>3DS Max</Progress>
                                            </div>
                                        </td>
                                    </tr>
                                </table>
                                <hr className="jumbotron-hr" />
                                <h2 style={{ textAlign: "center" }}>Other <span>Software</span></h2>
                                <table style={{ width: "100%", textAlign: "center" }}>
                                    <tr>
                                        <td>Operating Systems</td>
                                        <td>IDEs</td>
                                        <td>Miscellaneous</td>
                                    </tr>
                                    <tr style={{ height: 200, textAlign: "center" }}>
                                        <td>
                                            <div style={{ transformOrigin: "top left", transform: "rotate(-90deg)", position: "relative", top: 170, left: "calc(50% - 50px)" }}>
                                                <Progress color="primary" value={75} style={skills_bar_style}>Ubuntu</Progress>
                                                <Progress color="primary" value={75} style={skills_bar_style}>RedHat</Progress>
                                                <Progress color="primary" value={75} style={skills_bar_style}>CentOS</Progress>
                                                <Progress color="success" value={100} style={skills_bar_style}>Windows</Progress>
                                                <Progress color="primary" value={90} style={skills_bar_style}>Android</Progress>
                                            </div>
                                        </td>
                                        <td>
                                            <div style={{ transformOrigin: "top left", transform: "rotate(-90deg)", position: "relative", top: 170, left: "calc(50% - 50px)" }}>
                                                <Progress color="success" value={95} style={skills_bar_style}>Visual Studio</Progress>
                                                <Progress color="success" value={100} style={skills_bar_style}>Visual Studio Code</Progress>
                                                <Progress color="danger" value={90} style={skills_bar_style}>IntelliJ (Android Studio, PyCharm)</Progress>
                                                <Progress color="danger" value={90} style={skills_bar_style}>Eclipse</Progress>
                                                <Progress color="primary" value={75} style={skills_bar_style}>Vim</Progress>
                                            </div>
                                        </td>
                                        <td>
                                            <div style={{ transformOrigin: "top left", transform: "rotate(-90deg)", position: "relative", top: 159, left: "calc(50% - 40px)" }}>
                                                <Progress color="info" value={100} style={skills_bar_style}>Git</Progress>
                                                <Progress color="success" value={100} style={skills_bar_style}>Microsoft Office (Word, Excel, etc.)</Progress>
                                                <Progress color="warning" value={100} style={skills_bar_style}>Google Suite</Progress>
                                                <Progress color="danger" value={100} style={skills_bar_style}>Adobe Creative Cloud</Progress>
                                            </div>
                                        </td>
                                    </tr>
                                </table>
                                <hr className="jumbotron-hr" />
                                <h2 style={{ color: "rgba(255, 255, 255, 1)" }}>CSC<span>@</span>SBHS</h2>
                                <p className="lead">
                                    I truly believe that anyone can program; thus, I make it my goal to teach others the raw power of technology through the Computer Science Club at the South Brunswick High School. I seek to inspire my peers to create applications that transcend our wildest imaginations. Only then can we work to innovate our world, and make it better than we found it.
                                </p>
                            </Container>
                        </div>
                    </Jumbotron>
                    <Jumbotron style={{ backgroundColor: "black", height: 50 }}>
                    </Jumbotron>
                    <Jumbotron fluid style={{ backgroundColor: NAVBAR_BG_COLOR, minHeight: window.innerHeight }}>
                        <Container>
                            <ScrollableAnchor id="projects">
                                <h1 className="display-3" style={{ color: "rgba(255, 255, 255, 1)" }}>My <span>Projects</span></h1>
                            </ScrollableAnchor>
                            <p className="lead">
                            </p>
                            <Row>
                                <Col sm={6}>
                                    <Card style={{ border: 0, height: 200, overflow: "hidden" }}>
                                        <CardImg src={backgroundProjectSBHSMaps} style={{ position: "absolute", width: "100%", height: "auto", overflow: "hidden" }} />
                                        <CardImgOverlay style={{ backgroundColor: "rgba(0, 0, 0, 0.75)" }}>
                                            <CardTitle>SBHS Maps</CardTitle>
                                            <CardText>Maps, for school</CardText>
                                            <div style={{ position: "absolute", left: 25, bottom: 25, width: "calc(100% - 50px)", textAlign: "right" }}>
                                                <Badge color="info" style={{ display: "inline", marginLeft: 5 }}>React.JS</Badge>
                                                <Badge color="info" style={{ display: "inline", marginLeft: 5 }}>Java</Badge>
                                                <div style={{ display: "inline-block", marginLeft: 15, width: 1, height: 10, background: "rgba(255, 255, 255, 0.25)" }}></div>
                                                <Button outline color="secondary" style={{ border: 0 }}>Check it out</Button>
                                                <Button outline color="primary" style={{ border: 0 }}>GitHub</Button>
                                            </div>
                                        </CardImgOverlay>
                                    </Card>
                                </Col>
                                <Col sm={6}>
                                    <Card style={{ border: 0, height: 200, overflow: "hidden" }}>
                                        <CardImg src={backgroundProjectBench} style={{ position: "absolute", width: "100%", height: "auto", overflow: "hidden" }} />
                                        <CardImgOverlay style={{ backgroundColor: "rgba(0, 0, 0, 0.75)" }}>
                                            <CardTitle>Graph Workbench</CardTitle>
                                            <CardText>An online mathematical graphing utility implementing Dijkstra's algorithm</CardText>
                                            <div style={{ position: "absolute", left: 25, bottom: 25, width: "calc(100% - 50px)", textAlign: "right" }}>
                                                <Badge color="info" style={{ display: "inline", marginLeft: 5 }}>React.JS</Badge>
                                                <div style={{ display: "inline-block", marginLeft: 15, width: 1, height: 10, background: "rgba(255, 255, 255, 0.25)" }}></div>
                                                <Button outline color="secondary" style={{ border: 0 }}>Check it out</Button>
                                                <Button outline color="primary" style={{ border: 0 }}>GitHub</Button>
                                            </div>
                                        </CardImgOverlay>
                                    </Card>
                                </Col>
                            </Row>
                            <Row style={{ marginTop: 30 }}>
                                <Col sm={4}>
                                    <Card style={{ border: 0, height: 200, overflow: "hidden" }}>
                                        <CardImg src={backgroundProjectBench} style={{ position: "absolute", width: "100%", height: "auto", overflow: "hidden" }} />
                                        <CardImgOverlay style={{ backgroundColor: "rgba(0, 0, 0, 0.5)" }}>
                                            <CardTitle>Fractals</CardTitle>
                                            <CardText>An online mathematical graphing utility implementing Dijkstra's algorithm</CardText>
                                            <div style={{ position: "absolute", left: 25, bottom: 25, width: "calc(100% - 50px)", textAlign: "right" }}>
                                                <Badge color="info" style={{ display: "inline", marginLeft: 5 }}>React.JS</Badge>
                                                <div style={{ display: "inline-block", marginLeft: 15, width: 1, height: 10, background: "rgba(255, 255, 255, 0.25)" }}></div>
                                                <Button outline color="primary" style={{ border: 0 }}>GitHub</Button>
                                            </div>
                                        </CardImgOverlay>
                                    </Card>
                                </Col>
                                <Col sm={4}>
                                    <Card style={{ border: 0, height: 200, overflow: "hidden" }}>
                                        <CardImg src={backgroundProjectBench} style={{ position: "absolute", width: "100%", height: "auto", overflow: "hidden" }} />
                                        <CardImgOverlay style={{ backgroundColor: "rgba(0, 0, 0, 0.5)" }}>
                                            <CardTitle>Matrix Engine</CardTitle>
                                            <CardText>A lightweight graphics engine for Java based applications, extending the 2D capabilities of Java Swing to 3D</CardText>
                                            <div style={{ position: "absolute", left: 25, bottom: 25, width: "calc(100% - 50px)", textAlign: "right" }}>
                                                <Badge color="info" style={{ display: "inline", marginLeft: 5 }}>Java</Badge>
                                                <div style={{ display: "inline-block", marginLeft: 15, width: 1, height: 10, background: "rgba(255, 255, 255, 0.25)" }}></div>
                                                <Button outline color="primary" style={{ border: 0 }}>GitHub</Button>
                                            </div>
                                        </CardImgOverlay>
                                    </Card>
                                </Col>
                                <Col sm={4}>
                                    <Card style={{ border: 0, height: 200, overflow: "hidden" }}>
                                        <CardImg src={backgroundProjectBench} style={{ position: "absolute", width: "100%", height: "auto", overflow: "hidden" }} />
                                        <CardImgOverlay style={{ backgroundColor: "rgba(0, 0, 0, 0.5)" }}>
                                            <CardTitle>Galaxy Globe</CardTitle>
                                            <CardText>Spherical globe and environment</CardText>
                                            <div style={{ position: "absolute", left: 25, bottom: 25, width: "calc(100% - 50px)", textAlign: "right" }}>
                                                <Badge color="info" style={{ display: "inline", marginLeft: 5 }}>C#</Badge>
                                                <div style={{ display: "inline-block", marginLeft: 15, width: 1, height: 10, background: "rgba(255, 255, 255, 0.25)" }}></div>
                                                <Button outline color="primary" style={{ border: 0 }}>GitHub</Button>
                                            </div>
                                        </CardImgOverlay>
                                    </Card>
                                </Col>
                            </Row>
                        </Container>
                    </Jumbotron>
                    <Jumbotron>
                        <Container>
                            <p className="lead" style={{ textAlign: "center" }}>Copyright © 2017 Vanish Studios</p>
                        </Container>
                    </Jumbotron>
                </div>
            </div >
        );
    }
}
export default App;



